.header {

    background-color: #014e56;

    .header-container {
        display: flex;
        justify-content: space-between;
        height: 200px;
        align-items: center;

       
    }
}
.page-nav{
    align-items: center;
    justify-content: space-between;
    display: flex;
    gap: 5em;
    font-family: LivvicSemiBold;
    font-size: 18px;
    font-weight: 400;
    font-style: normal;

    a{
        text-decoration: none;
        color: white;
    }
    .in-page-nav{
        display: flex;
        gap: 2.5em;
        align-items: center;
        line-height: 26px;
    }
    .logo{
        height: 40px;
    }
}